.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1em;
    margin-bottom: .5em;
    letter-spacing: 0.01em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  select,
  input,
  textarea{
    font-family: $font1;
    border-radius: .25em;
    border: 1px solid $violet-gray;
    padding: 0 1em;
    height: 2.5em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $black;
    font-weight: 300;
    padding-right: 2.875em;
    background-size: 1.25em auto;
    background-repeat: no-repeat;
    background-position: right 1em center;
    &::placeholder {
      color: #929798;
    }
    &:focus {
      border-color: #9CA0A0;
      box-shadow: 0 .25em .4375em 0 rgba(47, 63, 67, 0.03), 0 .25em .3125em 0 rgba(0, 0, 0, 0.02) inset;
    }
    &:disabled{

    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0;
    padding: 0 1em;
    white-space: nowrap;
    &.open{
      //&:before{
      //  transform: rotate(180deg);
      //  opacity: 1;
      //}
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .75em;
      height: .75em;
      background-image: url(../img/arrow2.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
      opacity: .25;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      box-shadow: none;
      border: $border;
      margin-top: .125em;
      border-radius: 0;
      width: calc(100% + 2px);
      left: -1px;
      background: #FFF;
      max-height: 8.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 2.5em;
      font-size: 1em;
      line-height: 157%;
      padding: 0 1em;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $violet-gray;
        font-weight: 400;
        color: $black;
      }
      &.selected.focus{
        background-color: $primary;
        color: $white;
      }
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/arrow2.svg);
    background-size: .75em auto;
    background-repeat: no-repeat;
    background-position: right 1em center;
    &::-ms-expand {
      display: none; // IE
    }
    option:hover {
      box-shadow: 0 0 10px 100px $primary inset;
    }
  }
  textarea{
    padding-top: .625em;
    padding-bottom: .625em;
    height: 5em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  &._success{
    input{
      background-image: url(../img/check2.svg);
    }
  }
  &._error{
    input{
      background-image: url(../img/error.svg);
    }
  }
  &__info{
    position: absolute;
    right: 1em;
    bottom: .625em;
    font-size: 1em;
    line-height: 120%;
    letter-spacing: .01em;
    color: $primary;
    button{
      color: inherit;
    }
  }
}

.pass-toggle{
  position: absolute;
  bottom: .625em;
  right: 1em;
  width: 1.25em;
  height: 1.25em;
  @include center;
  img,
  svg{
    width: 100%;
    @include centerAbsolute;
    transition: all .3s;
  }
  ._view2{
    opacity: 0;
  }
  &._active{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.5em;
    min-height: 1em;
    font-weight: 300;
    span{
      font-size: .875em;
      line-height: 120%;
      letter-spacing: .01em;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid $gray-dark;
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
      font-weight: 400;
      color: $primary;
    }
  }
  input:hover~label:before{
    border-color: $primary;
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check.svg);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7.5625rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #E9EDF2;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #E9EDF2;
    border-right: 1px solid #E9EDF2;
    color: $black;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}


