.support-service{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 32.5em) 32.5em;
    background-color: $white;
    border-radius: .5em;
  }
  &__col{
    padding: 3em 5em;
  }
  &__title{
    font-size: 2.5em;
    letter-spacing: 0.01em;
  }
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    border-radius: 0 .5em .5em 0;
  }
  &__items{
    margin-top: 2.5em;
    &-col{
      display: flex;
      flex-direction: column;
    }
  }
}

.contact-item{
  @include grid100gap(.75em);
  margin-bottom: 1.5em;
  &:last-child{
    margin-bottom: 0;
  }
  &__title{
    font-weight: 300;
    font-size: 1em;
    color: $gray-dark;
    letter-spacing: 0.01em;
  }
  &__links{
    @include grid100gap(.75em);
  }
  &__text,
  &__link{
    display: flex;
    font-size: 1.5em;
    line-height: 117%;
    letter-spacing: 0.01em;
  }
  &__link{
    color: $primary;
  }
}

.address-card{
  background-color: $white;
  box-shadow: $shadow;
  padding: 2em 2.5em;
  border-radius: .5em;
  &__city{
    font-size: 1.75em;
    letter-spacing: 0.01em;
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.75em);
    font-weight: 300;
  }
  &__item{
    font-size: 1em;
    letter-spacing: 0.01em;
    .primary-text{
      font-weight: 400;
    }
  }
}

.time-warning2{
  &__inner{
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    justify-content: space-between;
    gap: 1em;
    border: 1px dashed $primary;
    border-radius: .5em;
    padding: 2em 3em;
  }
  &__text{
    font-size: 1.5em;
    line-height: 140%;
    letter-spacing: 0.01em;
    color: $primary;
  }
}


@media screen and (max-width: 1400px){
  .support-service{
    &__grid{
      grid-template-columns: calc(100% - 28.4375em) 28.4375em;
    }
    &__col{
      padding: 2.5em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__items{
      margin-top: 1.5em;
    }
  }

  .contact-item{
    &__text,
    &__link{
      font-size: 1.25em;
    }
  }

  .address-card{
    padding: 1.5em;
    &__title{
      font-size: 1.5em;
    }
    &__items{
      margin-top: 1em;
    }
  }

  .time-warning2{
    &__inner{
      padding: 1.5em 2.5em;
      grid-template-columns: 40.5em auto;
    }
    &__text{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .support-service{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 1.5em 1em;
    }
    &__col2{
      display: none;
    }
  }

  .contact-item{
    margin-bottom: 1em;
  }

  .addresses{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .1875em));
      gap: .375em;
    }
  }

  .address-card{
    padding: 1.5em 1em;
    &__city{
      font-size: 1.25em;
    }
    &__item{
      line-height: 120%;
    }
  }

  .time-warning2{
    &__inner{
      grid-template-columns: 100%;
      padding: 1.5em 1em;
    }
    &__text{
      font-size: 1em;
      line-height: 160%;
    }
  }
}


@media screen and (max-width: 576px){
  .support-service{
    &__items{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .addresses{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
