.h1{
  font-size: 4em;
  letter-spacing: 0.01em;
}

.h2{
  font-size: 3em;
  letter-spacing: 0.01em;
}

.h3{
  font-size: 2em;
  line-height: 120%;
  letter-spacing: 0.01em;
}

.text24{
  font-size: 1.5em;
  line-height: 140%;
  letter-spacing: 0.01em;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
  letter-spacing: 0.01em;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 121%;
  letter-spacing: 0.01em;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.gray-dark-text{
  color: $gray-dark;
}

.dark-text{
  color: $dark;
}

.primary-text{
  color: $primary;
}


@media screen and (max-width: 1400px){
  .h1{
    font-size: 3em;
  }

  .h2-laptop,
  .h2{
    font-size: 2em;
  }

  .text20-laptop{
    font-size: 1.25em;
  }

  .text16-laptop{
    font-size: 1em;
  }
}


@media screen and (max-width: 992px){
  .h1{
    font-size: 2em;
  }

  .h3{
    font-size: 1.25em;
  }

  .h2-tablet,
  .h2{
    font-size: 1.5em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
