.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .6;
    backdrop-filter: blur(16px);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $white;
        width: 32.5em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: .5em;
        display: inline-block;
    }
    &__top{
        padding-right: 2em;
    }
    &__title{
        font-size: 1.5em;
        letter-spacing: 0.01em;
    }
    .close-btn{
        width: 1em;
        @include center;
        position: absolute;
        top: 2.75em;
        right: 2.5em;
        svg{
            width: 100%;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__nav{
        margin-top: 2em;
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: .75em 1.25em;
        &._type2{
            grid-template-columns: 100%;
            gap: .5em;
        }
    }
    &__btn{
        img{
            width: 1.25em;
            margin-right: .75em;
        }
    }
    &__desc{
        font-size: 1em;
        line-height: 120%;
        letter-spacing: .01em;
        color: $dark;
        margin-top: .75em;
    }
    &__hint {
        margin-top: .75em;
        font-size: 1em;
        line-height: 140%;
        color: $dark;
    }
}

.modal-form{
    margin-top: 2em;
    &__fields{
        @include grid100gap(1em);
        margin-bottom: 1em;
    }
    &__hint{
        margin-top: .75em;
        font-weight: 300;
        font-size: 1em;
        line-height: 120%;
        color: $dark;
        &-btn{
            display: inline;
            font-weight: 400;
            color: $primary;
        }
    }
}

._full{
    grid-column: 1/-1;
}

.call-modal{
    .modal__dialog{
        padding-top: 5em;
    }
    .modal__nav{
        margin-top: 1em;
    }
    &__phone{
        padding: 1em;
        @include center;
        border-radius: .25em;
        border: 1px solid $gray;
        &-title{
            font-size: 1.25em;
            line-height: 140%;
            letter-spacing: 0.01em;
        }
    }
}

.code-field{
    display: flex;
    .input-group{
        display: flex;
        margin-right: 1.5em;
        &:last-child{
            margin-right: 0;
        }
        input{
            padding: 0;
            font-size: 1.5em;
            width: 2em;
            height: 2em;
            text-align: center;
            margin-right: .33em;
            &:last-child{
                margin-right: 0;
            }
        }
    }
}

.logout-modal{
    .modal__top{
        padding-right: 0;
    }
    .modal__title{
        text-align: center;
    }
    .success__icon{
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .modal__nav{
        margin-top: 1.5em;
    }
}

.reg-modal{
    .modal__dialog{
        width: 49.375em;
    }
    .modal-form__fields{
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 1em 1.25em;
    }
    .m-check{
        margin-top: .75em;
    }
}


@media screen and (max-width: 1400px){
    .modal{
        &__dialog {
            padding: 2em;
            width: 34.375em;
        }
        .close-btn{
            right: 2em;
            top: 2.25em;
        }
        &__nav{
            margin-top: 1.5em;
        }
    }

    .modal-form{
        margin-top: 1.5em;
    }

    ._full-laptop{
        grid-column: 1/-1;
    }

    .reg-modal {
        .modal__dialog {
            width: 34.375em;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &__dialog {
            padding: 1em;
            width: 100%;
            height: $vh;
            overflow-y: auto;
            border-radius: 0;
        }
        .close-btn{
            width: 1.5em;
            top: 1em;
            right: 1em;
        }
        &__nav{
            grid-template-columns: 100%;
        }
        &__desc{
            br{
                display: none;
            }
        }
    }

    .modal-form{
        margin-top: 2em;
    }

    .code-field{
        .input-group{
            input{
                margin-right: .17em;
            }
        }
    }

    .logout-modal{
        .modal__dialog{
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    .lk-modal{
        .modal__nav{
            gap: .75em;
        }
        .modal-form__hint{
            line-height: 140%;
            button{
                display: block;
                margin-top: .25em;
            }
        }
    }
    .reg-modal{
        .modal-form__fields{
            grid-template-columns: 100%;
        }
    }
}
