@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 2.6875em;
    border-radius: .25em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        letter-spacing: 0.01em;
    }
}

.m-btn-violet{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: $violet-light;
        color: $white;
    }
    &:disabled{
        background-color: $gray;
        color: $white;
    }
}

.m-btn-violet-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        border-color: $violet-light;
        color: $violet-light;
        path{
            fill: $violet-light;
        }
    }
    &:disabled{
        border-color: $gray;
        color: $gray;
        path{
            fill: $gray;
        }
    }
}
