.page-top{
    padding: 0 0 2.5em;
}

.breadcrumbs {
    margin-bottom: 5em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: $gray;
        &:not(:first-child) {
            margin-left: 1.25em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: .125em;
                right: 100%;
                margin-right: .625em;
                font-size: .875em;
                line-height: 130%;
                font-weight: 500;
            }
        }
        &:last-child{
            color: $primary;
        }
    }
    a{
        font-size: .875em;
        line-height: 130%;
    }
}


@media screen and (max-width: 1400px){
    .page-top{
        padding-bottom: 1.5em;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}


