.error404{
  padding: 6em 1em 7.5em;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    width: 50.375em;
    max-width: 100%;
    margin-bottom: -2em;
  }
  &__text{
    font-size: 1.25em;
    line-height: 140%;
    letter-spacing: 0.01em;
    color: $dark;
  }
  &__link{
    margin-top: 1em;
  }
}


@media screen and (max-width: 1400px){
  .error404{
    &__img{
      width: 46.25em;
    }
    &__text{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    &__img{
      margin-bottom: 0;
    }
    &__link{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    &__text{
      font-size: 1em;
    }
  }
}
