.about{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 32.5em) 32.5em;
    border-radius: .5em;
    box-shadow: 0 .25em 1em 0 rgba(132, 132, 132, 0.1);
    background-color: $white;
  }
  &__col{
    padding: 5em;
  }
  &__col2{
    padding: 5em 2.5em;
    background-color: $primary;
    color: $white;
    border-radius: 0 .5em .5em 0;
  }
  &__text{
    margin-top: 1.5em;
  }
  .m-nav{
    margin-top: 2em;
  }
  &__text2{
    margin-top: .9em;
    font-size: 1.125em;
    line-height: 160%;
    letter-spacing: 0.01em;
  }
}

.main{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 45.5em) 44.25em;
    gap: 1.25em;
    padding: 4.75em 5em 8.625em;
    background-color: $white;
    border-radius: .5em;
  }
  &__content{
    padding-top: 4em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  .h1{
    max-width: 9em;
  }
  &__desc{
    margin-top: 1.5em;
  }
  .m-nav{
    margin-top: 2em;
  }
}

.daily-route{
  &__grid{
    margin-top: 2.5em;
    &:first-child{
      margin-top: 0;
    }
  }
}

.route-card{
  display: flex;
  align-items: center;
  padding-left: 2em;
  min-height: 5.75em;
  background-color: $white;
  border-radius: .5em;
  &__text{
    flex-grow: 1;
    padding: .25em .25em .25em 0;
    color: $gray-dark;
  }
  &__btn{
    flex-shrink: 0;
    border-radius: 0 .5em .5em 0;
    padding: 0;
    width: 6em;
    height: 100%;
    img{
      width: 2em;
    }
  }
}


@media screen and (max-width: 1400px){
  .about{
    &__grid{
      grid-template-columns: calc(100% - 22.5em) 22.5em;
    }
    &__col{
      padding: 5em 3.25em 5em 6em;
    }
    &__col2{
      padding: 4.75em 1.5em;
    }
    &__text2{
      font-size: 1em;
      margin-top: 1em;
    }
  }

  .main{
    &__inner{
      grid-template-columns: calc(100% - 33.75em) 32.5em;
      padding: 5em 2em 5em 6em;
    }
    &__content{
      padding-top: 0;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .daily-route{
    &__grid{
      margin-top: 1.5em;
    }
  }

  .route-card{
    padding-left: 1em;
    min-height: 4.375em;
    &__btn{
      width: 4.375em;
      img{
        width: 1.5em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 2em 1em;
    }
    &__col2{
      display: none;
    }
    &__text{
      margin-top: 1em;
    }
    .m-nav{
      margin-top: 1.5em;
    }
    .h3{
      font-size: 1.5em;
    }
  }

  .main{
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
      padding: 2em 1em;
    }
    &__img{
      display: none;
    }
    .m-nav{
      margin-top: 1.5em;
    }
  }

  .route-card{
    padding-left: 0;
    &__text{
      padding: 1.14em;
    }
  }
}


@media screen and (max-width: 576px){
  .daily-route{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
