.logo{
  display: block;
  flex-shrink: 0;
  width: 15em;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding-bottom: 1.5em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $violet-gray;
    padding: 1.5em 0;
  }
  &__logo{
    margin-right: 3em;
  }
}

.burger{
  display: none;
}

.menu{
  flex-grow: 1;
  &__scroll{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
}

.nav{
  display: flex;
  &__link{
    font-size: 1em;
    line-height: 140%;
    letter-spacing: .01em;
    margin-right: 2em;
    color: $gray-dark;
  }
}

.action{
  display: flex;
  &__contact{
    margin-right: .75em;
    svg{
      width: 1.25em;
      display: none;
    }
  }
  ._view2{
    display: none;
  }
}

._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}


@media screen and (max-width: 1400px){
  .logo{
    width: 13.5em;
  }

  .header{
    padding-bottom: 1em;
    &__inner{
      padding: 1em 0;
    }
  }

  .action{
    &__contact{
      padding: 0;
      width: 2.6875em;
      span{
        display: none;
      }
      svg{
        display: block;
      }
    }
    @media screen and (min-width: 993px){
      ._view1{
        display: none;
      }
      ._view2{
        display: flex;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .logo{
    width: 9.625em;
  }

  .header{
    padding-bottom: .5em;
    &__inner{
      border: none;
    }
    &__logo{
      position: relative;
      z-index: 11;
    }
    .action{
      order: -1;
      padding: 0 0 .875em;
      flex-wrap: wrap;
      justify-content: space-between;
      &__contact{
        width: auto;
        height: auto;
        border: none;
        color: $dark;
        &:hover,
        &:active{
          color: $dark;
        }
        svg{
          display: none;
        }
        span{
          display: block;
          font-size: .875em;
          line-height: 140%;
          letter-spacing: 0.01em;
        }
      }
      &__lk{
        width: 100%;
        margin-top: 1.5em;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 3.5em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .nav{
    width: 100%;
    display: block;
    &__link{
      border-bottom: 1px solid #efefef;
      padding: .625em 0;
      margin: 0 0 .375em;
      width: 100%;
      display: block;
      color: $black;
    }
  }
}


