.m-section{
  padding: 5.625em 0;
  &._first-section{
    padding-top: 0;
  }
}

.slider{
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 1.25em;
    }
    &-scrollbar{
      display: none;
    }
  }
}

.reviews{
  overflow: hidden;
}

.swiper-button{
  width: 2em;
  height: 2em;
  @include center;
  svg{
    width: 100%;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}

.review-slider{
  margin-top: 2.5em;
  position: relative;
  .swiper{
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.5em;
      display: flex;
      align-items: center;
    }
    &-button{
      position: static;
      margin: 0 0 0 5em;
    }
    &-slide{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      width: calc(100% / 3);
      &-active{
        opacity: 1;
        pointer-events: unset;
      }
      @media screen and (min-width: 993px){
        &-next+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
      @media screen and (min-width: 577px){
        &-next{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.review-card{
  padding: 2em 1.5em;
  background-color: $white;
  box-shadow: 0 .25em 1em 0 rgba(132, 132, 132, 0.1);
  border-radius: .5em;
  &__city{
    font-size: .875em;
    line-height: 150%;
    letter-spacing: 0.01em;
    font-weight: 300;
    color: $gray;
  }
  &__name{
    font-size: 1.5em;
    line-height: 110%;
    letter-spacing: 0.01em;
    margin-top: .67em;
  }
  &__content{
    margin-top: 1.5em;
    height: 13em;
    overflow: hidden;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 1.6em;
      width: 100%;
      background-color: rgba($white,.7);
      left: 0;
      bottom: 0;
    }
  }
  &__text{
    font-weight: 300;
    font-size: 1em;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: $dark;
  }
  &__btn{
    display: flex;
    align-items: center;
    margin-top: .5em;
    font-weight: 300;
    color: $primary;
    &:hover{
      color: $violet-light;
      path{
        fill: $violet-light;
      }
    }
    svg{
      width: 1.5em;
      margin-right: 1em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
      letter-spacing: 0.01em;
    }
  }
}

.m-nav{
  display: flex;
  margin-top: 2.5em;
}

.review-modal{
  .modal__dialog{
    width: 66.25em;
  }
  .review-card__content{
    height: unset;
    &:before{
      display: none;
    }
  }
}


@media screen and (max-width: 1400px){
  .m-section{
    padding: 4.375em 0;
  }

  .review-slider{
    margin-top: 1.5em;
    .swiper-nav{
      margin-bottom: 1.5em;
    }
  }

  .review-card{
    padding: 1.5em;
    &__content{
      margin-top: 1em;
    }
  }

  .m-nav{
    margin-top: 1.5em;
  }

  .review-modal{
    .modal__dialog{
      width: 46.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 2.5em 0;
  }

  .slider{
    .swiper{
      &-scrollbar{
        height: .125em;
        display: block;
        margin-top: 1.5em;
        background-color: $gray-white;
        &-drag{
          background-color: $primary;
        }
      }
    }
  }

  .review-slider{
    .swiper{
      &-slide{
        width: 50%;
        opacity: 1;
      }
      &-nav{
        display: none;
      }
    }
  }

  .review-card{
    padding: 1.5em;
    &__name{
      font-size: 1.25em;
      margin-top: .6em;
    }
    &__content{
      margin-top: .75em;
    }
  }

  .m-nav{
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .review-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
