.routes-page{
  margin-top: -1em;
}

.route-stops{
  display: flex;
  flex-wrap: wrap;
  margin-top: .75em;
  width: calc(100% + .5em);
  &__item{
    margin: .75em .5em 0 0;
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      background-color: $primary;
      width: .75em;
      height: .125em;
      margin-right: .5em;
    }
    &:first-child:before{
      display: none;
    }
    &:first-child,
    &:last-child{
      .route-stops__badge{
        background-color: $primary;
        color: $white;
      }
    }
  }
  &__badge{
    padding: 0 .9375em;
    height: 2.5em;
    border-radius: .25em;
    border: $border2;
    display: flex;
    align-items: center;
    font-weight: 300;
    span{
      font-size: 1em;
      letter-spacing: 0.01em;
    }
  }
}

