.air-datepicker{
  width: 15.75em;
  box-sizing: border-box;
  border-radius: .5em;
  border: 1px solid $gray;
  font-size: 1em;
  padding: .75em;
  &--pointer{
    display: none;
  }
  &--content{
    padding: 0;
  }
}

.air-datepicker-nav{
  align-items: center;
  justify-content: flex-end;
  padding: 0;
  min-height: unset;
  border: none;
  margin-bottom: .75em;
  &--title{
    margin-right: auto;
    order: -1;
    font-size: 1.25em;
    color: $dark;
    padding: 0;
    position: relative;
    &:after{
      //content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      background-color: $white;
      height: 100%;
      width: .25em;
    }
    i{
      //display: none;
    }
  }
  &--action{
    width: .75em;
    height: .75em;
    margin-left: 1.5em;
    svg{
      display: none;
    }
    &[data-action="prev"]{
      background-image: url(../img/prev.svg);
      @include bgImgSize(100%);
      &:hover{
        background-image: url(../img/prev.svg);
        @include bgImgSize(100%);
        background-color: transparent;
      }
    }
    &[data-action="next"]{
      background-image: url(../img/next.svg);
      @include bgImgSize(100%);
      &:hover{
        background-image: url(../img/next.svg);
        @include bgImgSize(100%);
        background-color: transparent;
      }
    }
  }
}

.air-datepicker-body{
  &--day-names{
    margin: 0;
  }
  &--day-name{
    margin: 0;
    font-size: 1em;
    letter-spacing: 0.01em;
    color: $gray-dark;
    text-transform: lowercase;
    height: 2.25em;
  }
  .air-datepicker-body--cells{
    grid-auto-rows: unset;
    align-items: center;
  }
}

.air-datepicker-cell{
  font-size: .875em;
  letter-spacing: 0.01em;
  height: 2.57em;
  @include center;
  &.-current-,
  &.-selected-{
    margin: 0 auto;
    height: 2em;
    width: 1.58em;
    border-radius: 7.14em;
    &.-month-,
    &.-year-{
      width: 100%;
    }
  }
  &.-current-{
    border: $border;
    color: $black;
  }
  &.-year-.-other-decade-,
  &.-day-.-other-month-{
    color: $gray;
  }
  &.-year-.-other-decade-.-selected-,
  &.-day-.-other-month-.-selected-,
  &.-selected-{
    background-color: $primary;
    color: $white;
  }
}

.routes-table{
  margin-top: 5em;
  table{
    width: 100%;
    border-spacing: 0 .5em;
  }
  th,td{
    width: calc(100% / 6);
    &:nth-child(1){
      padding-left: 1.5em;
    }
    &:last-child{
      padding-right: 1.5em;
      width: 14em;
    }
  }
  th{
    text-align: left;
    font-weight: 400;
    color: $gray-dark;
    padding-bottom: 1em;
  }
  td{
    padding-top: 1em;
    padding-bottom: 1em;
    &:first-child{
      border-radius: .5em 0 0 .5em;
    }
    &:last-child{
      border-radius: 0 .5em .5em 0;
    }
  }
  &__th{
    font-size: 1em;
    line-height: 100%;
    letter-spacing: 0.01em;
    ._view2{
      display: none;
    }
  }
  tbody{
    tr{
      position: relative;
    }
  }
  ._bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: $white;
    border-radius: .5em;
  }
}

.route-row{
  &__title,
  &__td{
    font-size: 1.5em;
    line-height: 100%;
    letter-spacing: 0.01em;
  }
  &__btn{
    width: 11.625em;
    padding: 0;
    margin-left: auto;
  }
  &__title{
    display: none;
  }
}


@media screen and (max-width: 1400px){
  .routes-page{
    margin-top: -.5em;
  }

  .route-stops{
    width: calc(100% + .25em);
    &__item{
      margin-right: .25em;
      &:before{
        width: .5em;
      }
    }
    &__badge{
      padding: 0 .5625em;
    }
  }

  .routes-table{
    margin-top: 3em;
    th,td{
      &:nth-child(1){
        padding-left: 1em;
      }
      &:last-child{
        padding-right: 1em;
        width: 13em;
      }
    }
    &__th{
      padding-bottom: .25em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .route-row{
    &__title,
    &__td{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .route-stops{
    margin-top: 1em;
    &__item{
      margin: .25em .5em 0 0;
      &:before{
        display: none;
      }
    }
    &__badge{
      padding: 0 .3125em;
      height: 1.625em;
      span{
        font-size: .875em;
      }
    }
  }

  .route-filter{
    margin-top: 2.5em;
  }

  .routes-table{
    margin-top: 2.5em;
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(.5em);
    }
    tr{
      padding: 1.5em 1em;
      background-color: $white;
      box-shadow: $shadow;
      @include grid100gap(1em);
      border-radius: .5em;
    }
    td{
      width: 100% !important;
      padding: 0 !important;
      display: flex;
      justify-content: space-between;
    }
  }

  .route-row{
    &__title{
      display: block;
      color: $gray-dark;
    }
    ._bg{
      display: none;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .route-filter{
    display: block;
    &__field{
      width: 100%;
    }
    &__btn{
      margin: 1em 0 0;
      width: 100%;
    }
  }

  .air-datepicker{
    width: calc(100vw - 2em);
  }
}
