.recover-page{
  &__link{
    width: 32.5em;
    max-width: 100%;
    margin-top: .75em;
    @media screen and (min-width: 577px){
      border: none;
      background-color: $white;
      box-shadow: $shadow;
      color: $gray-dark;
    }
  }
}

.success{
  flex-grow: 1;
  @include center;
  flex-direction: column;
  &__inner{
    padding: 2.5em;
    background-color: $white;
    box-shadow: $shadow;
    border-radius: .5em;
    width: 32.5em;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__icon{
    width: 7.5em;
    margin-bottom: 1em;
  }
  &__text{
    @include grid100gap(.75em);
  }
  &__title,
  &__desc{
    line-height: 100%;
    letter-spacing: 0.01em;
  }
  &__desc{
    color: $gray-dark;
  }
  &__link{
    width: 100%;
    margin-top: 1.5em;
  }
}

.recover-form{
  text-align: left;
  width: 100%;
  &__title{
    font-size: 1.5em;
    letter-spacing: .01em;
  }
  &__fields{
    margin-top: 2em;
    @include grid100gap(1em);
  }
  &__btn{
    margin-top: 2em;
    width: 100%;
  }
  &__hint{
    margin-top: .75em;
    text-align: center;
    color: $dark;
    button{
      font-size: 1em;
      line-height: 120%;
      color: $primary;
    }
  }
}


@media screen and (max-width: 1400px){
  .recover-page{
    &__link{
      width: 34.375em;
    }
  }

  .success{
    &__inner{
      padding: 2em;
      width: 34.375em;
    }
    &__icon{
      margin-bottom: 1.5em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .recover-page{
    flex-direction: column;
    align-items: unset;
    justify-content: space-between;
    padding-bottom: 1.5em;
    &__link{
      width: calc(100% - 2em);
      margin: 1em auto 0;
    }
  }

  .success{
    background-color: $white;
    &__inner{
      padding: 1em;
      width: 100%;
      box-shadow: unset;
    }
    &__link{
      margin-top: 1em;
    }
  }
}
