.about-main{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 32.5em) 32.5em;
    background-color: $white;
    border-radius: .5em;
  }
  &__img{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 0 .5em .5em 0;
    }
  }
  &__content{
    padding: 5em;
  }
  &__text{
    margin-top: 1.5em;
  }
}

.count-item{
  &__num{
    font-size: 5em;
    letter-spacing: 0.01em;
    color: $primary;
  }
  &__text{
    margin-top: .75em;
    color: $gray-dark;
  }
}

.about2{
  &__grid{
    display: grid;
    grid-template-columns: 32.5em calc(100% - 32.5em);
    background-color: $white;
    border-radius: .5em;
  }
  &__col2{
    padding: 5em;
  }
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
  .m-list{
    margin-top: 1.5em;
    @media screen and (min-width: 1401px){
      br{
        display: none;
      }
    }
  }
}

.m-list{
  @include grid100gap(.75em);
  li{
    font-weight: 300;
    font-size: 1.25em;
    line-height: 160%;
    letter-spacing: 0.01em;
    padding-left: 1.2em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: .5em;
      width: .6em;
      height: .6em;
      background-color: $primary;
      border-radius: 50%;
    }
  }
}


@media screen and (max-width: 1400px){
  .about-main{
    &__inner{
      grid-template-columns: calc(100% - 22.5em) 22.5em;
    }
    &__content{
      padding: 2.5em;
    }
    .h3{
      max-width: 16.25em;
    }
  }

  .count-item{
    &__num{
      font-size: 3.5em;
    }
  }

  .about2{
    &__grid{
      grid-template-columns: 22.5em calc(100% - 22.5em);
    }
    &__col2{
      padding: 2.5em;
    }
  }

  .m-list{
    li{
      font-size: 1em;
      line-height: 140%;
      padding-left: 1.75em;
      &:before{
        left: .375em;
        top: .25em;
        width: .75em;
        height: .75em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about-main{
    &__inner{
      grid-template-columns: 100%;
      padding: 1.5em 1em;
    }
    &__content{
      padding: 0;
    }
    &__img{
      display: none;
    }
    &__text{
      margin-top: 1em;
    }
  }

  .count-item{
    &__num{
      font-size: 2.5em;
    }
    .text20{
      line-height: 120%;
    }
  }

  .about2{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      display: none;
    }
    &__col2{
      padding: 1.5em 1em;
    }
    .m-list{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-counts{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .count-item{
    border-bottom: $border;
    padding-bottom: 1em;
    &__text{
      margin-top: .5em;
    }
  }
}
