.footer{
  padding: 5.625em 0 1em;
}

.footer-bottom,
.footer-top,
.footer-nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-nav{
  margin-top: 2em;
  &__link{
    font-size: 1em;
    line-height: 121%;
    letter-spacing: 0.01em;
    color: $gray-dark;
  }
}

.footer-bottom{
  margin-top: 1.5em;
  padding-top: 1.5em;
  border-top: 1px solid $violet-gray;
  color: $gray-dark;
}


@media screen and (max-width: 1400px){
  .footer{
    padding-top: 4.375em;
  }

  .footer-nav{
    margin-top: 1.5em;
    &__link{
      font-size: .875em;
    }
  }

  .footer-bottom {
    margin-top: 1em;
    padding-top: 1em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 2.5em;
  }

  .footer-nav{
    flex-direction: column;
    &__link{
      font-size: 1em;
      margin: 0 0 1.5em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }

  .footer-bottom {
    flex-direction: column;
    &__conf,
    &__by{
      margin: .86em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer-top{
    display: block;
    .action{
      margin-top: 1.5em;
      @include grid100gap(.75em);
      &__contact{
        margin: 0;
        width: 100%;
        svg{
          display: none;
        }
        span{
          display: block;
        }
      }
      &__lk{
        order: -1;
      }
    }
  }
}
