.timetable{
  &._first-section{
    margin-top: -1em;
  }
  &__title{
    font-size: 1.5em;
    letter-spacing: 0.01em;
    color: $gray-dark;
  }
  &__items{
    margin-top: 2.5em;
    @include grid100gap(.75em);
  }
}

.timetable-item{
  padding: 1.5em 2.5em;
  background-color: $white;
  border-radius: .5em;
  box-shadow: 0 .125em 1em 0 rgba(132, 132, 132, 0.1);
  display: grid;
  grid-template-columns: 21.5625em 32.5em 21.5625em;
  justify-content: space-between;
  &__top{
    display: flex;
    align-items: center;
  }
  &__departure{
    min-width: 3.6em;
  }
  &__badge{
    display: flex;
    align-items: center;
    padding: 0 1.5em;
    color: $dark;
    height: 2.25em;
    border: 1px solid $primary;
    border-radius: .5em;
    span{
      font-size: 1em;
      letter-spacing: 0.01em;
    }
  }
  &__routes{
    margin-top: 1em;
    @include grid100gap(.5em);
    &-item{
      font-size: 1em;
      letter-spacing: 0.01em;
      font-weight: 300;
    }
  }
  &__col2{
    display: flex;
    justify-content: center;
  }
  &__place{
    width: calc(50% - .625em);
    margin-right: 1.25em;
    &:last-child{
      margin-right: 0;
    }
  }
  &__col3{
    justify-content: flex-end;
  }
  &__btn{
    margin-left: 2em;
  }
}

.time-warning{
  &__inner{
    padding: 1.375em 2.5em;
    border-radius: .5em;
    border: 1px dashed $primary;
    display: grid;
    grid-template-columns: calc(100% - 31.25em) 26.25em;
    gap: 5em;
    align-items: center;
  }
  &__text{
    line-height: 160%;
    color: $primary;
  }
  &__img{
    width: 100%;
    display: block;
  }
}


@media screen and (max-width: 1400px){
  .timetable{
    &__title{
      font-size: 1.25em;
    }
    &__items{
      margin-top: 1.5em;
      gap: .5em;
    }
  }

  .timetable-item{
    grid-template-columns: 15em 28.75em 10.625em;
    &__departure{
      min-width: 3.6em;
    }
    &__badge{
      padding: 0 1em;
      height: 2em;
    }
    &__col3{
      display: block;
    }
    &__btn{
      margin: 1em 0 0;
      width: 100%;
      padding: 0;
    }
    .h3{
      font-size: 1.5em;
    }
  }

  .time-warning{
    &__inner{
      padding: 2.5em;
      grid-template-columns: calc(100% - 21.25em) 20em;
      gap: 1.25em;
    }
    &__text{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .timetable {
    &__title {
      font-size: 1em;
    }
  }

  .timetable-item{
    grid-template-columns: 100%;
    padding: 1.5em 1em;
    &__top{
      align-items: flex-end;
      justify-content: space-between;
    }
    &__routes{
      margin-top: .75em;
      justify-content: space-between;
      display: flex;
      gap: unset;
    }
    &__col{
      padding-bottom: 1em;
      margin-bottom: 1em;
      border-bottom: $border;
    }
    &__col2{
      @include grid100gap(1em);
    }
    &__place{
      width: 100%;
      margin: 0;
      padding-bottom: 1em;
      border-bottom: $border;
    }
    &__col3{
      margin-top: 1.5em;
    }
  }

  .time-warning{
    &__inner{
      padding: 1.5em 1em;
      grid-template-columns: 100%;
    }
    &__col2{
      display: none;
    }
  }
}


