.order-info{
  padding: 3em;
  background-color: $white;
  border-radius: .5em;
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    color: $dark;
    font-weight: 300;
    font-size: 1.25em;
    line-height: 120%;
    letter-spacing: .01em;
    span{
      font-weight: 400;
      padding-right: .25em;
    }
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
  }
  &__btn{
    margin-right: .75em;
    &:last-child{
      margin: 0;
    }
  }
  &__selected{
    padding: 0 2.5em;
    height: 2.6875em;
    border-radius: .25em;
    border: 1px solid $primary;
    color: $primary;
    @include center;
    span{
      font-size: 1em;
      letter-spacing: 0.01em;
    }
    i{
      width: .5em;
      height: .5em;
      border-radius: 50%;
      background-color: $primary;
      flex-shrink: 0;
      margin: 0 .625em;
    }
  }
}

.bus-area{
  padding: 3em;
  background-color: $white;
  border-radius: .5em;
}

.bus{
  padding: 1em 2.1875em;
  border: $border;
  border-radius: .5em;
  display: grid;
  grid-template-columns:
          3em
          auto
          auto
          auto
          auto
          auto
          auto
          auto
          auto
          auto
          auto
          auto
          auto
  ;
  gap: .75em 1em;
  &__door{
    grid-row: span 2;
    @include grid100gap(.5em);
    span{
      background-color: $gray-white;
      border-radius: .25em;
      min-height: 1em;
      display: block;
    }
    &--center{
      width: calc(100% + 1em);
      margin-left: -.5em;
    }
  }
  &__seat{
    cursor: pointer;
    align-self: flex-start;
    padding-bottom: 100%;
    position: relative;
    color: $dark;
    &-inner{
      border: $border;
      border-radius: .25em;
      @include center;
      width: 100%;
      height: 100%;
      @include centerAbsolute;
    }
    span{
      font-size: 1.125em;
      letter-spacing: .03em;
    }
    &--selected{
      .bus__seat-inner{
        background-color: $primary;
        border-color: $primary;
        color: $white;
      }
    }
    &--booked{
      .bus__seat-inner{
        background-color: $yellow;
        border-color: $yellow;
        color: $white;
      }
    }
  }
  &__row{
    grid-column: span 11;
    background-color: $gray-white;
    border-radius: .25em;
  }
  &__driver{
    grid-row: span 2;
    @include center;
  }
  &__wheel{
    width: 100%;
    display: block;
  }
}

.seat-info{
  display: flex;
  margin-top: 1.5em;
  &__item{
    margin-right: 2.5em;
    font-weight: 300;
    display: flex;
    align-items: center;
    &:last-child{
      margin: 0;
    }
    &:before{
      content: '';
      width: 1.5em;
      height: 1.5em;
      border-radius: .25em;
      border: $border;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 110%;
      letter-spacing: .01em;
    }
    &--selected{
      &:before{
        background-color: $primary;
        border-color: $primary;
      }
    }
    &--booked{
      &:before{
        background-color: $yellow;
        border-color: $yellow;
      }
    }
    &--sold{
      &:before{
        background-color: $gray;
        border-color: $gray;
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .order-info{
    padding: 2.5em 1.5em;
    &__items{
      gap: .75em;
    }
    &__item{
      font-size: 1.125em;
      line-height: 140%;
    }
  }

  .bus-area{
    padding: 1.25em;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .bus{
    padding: 0;
    border: none;
    gap: .75em .5em;
    &__seat{
      padding-bottom: 90%;
      span{
        font-size: 1em;
      }
    }
    &__door{
      &--center{
        width: calc(100% + .5em);
        margin-left: -.25em;
      }
    }
  }

  .seat-info{
    margin-top: 2.5em;
    &__item{
      margin-right: 1.875em;
    }
  }
}


@media screen and (max-width: 992px){
  .order-info{
    padding: 1em;
    &__item{
      font-size: 1em;
      line-height: 140%;
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    &__grid{
      position: relative;
    }
  }

  .order-info{
    &__nav{
      width: 100%;
      display: block;
      padding: 1.5em 2em;
      position: absolute;
      bottom: 0;
      left: 0;
    }
    &__btn{
      width: 100%;
      margin-bottom: .5em;
    }
    &__selected{
      padding: 0 1em;
    }
  }

  .bus-area{
    padding-bottom: 8.875em;
  }

  .bus{
    //position: absolute;
    grid-template-columns:
            2em
            2em
            2em
            2em
            2em
            2em
            2em
            2em
            2em
            2em
            2em
            2em
            2em
  ;
    top: 0;
    left: 0;
    width: 32em;
    transform: rotate(90deg) translateY(60%) translateX(32%);
    &__wrap{
      height: 32em;
      position: relative;
    }
    &__seat{
      &-inner{
        span{
          transform: rotate(-90deg);
        }
      }
    }
    &__wheel{
      width: 2em;
    }
    &__door{
      max-width: 2em;
    }
  }

  .seat-info{
    margin-top: .5em;
    flex-wrap: wrap;
    justify-content: space-between;
    &__item{
      margin: .5em 0 0 0;
    }
  }
}
